import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Zap, PlusCircle, Loader2 } from "lucide-react"
import { addPoints } from "@/lib/firebase/wallet"
import { useToast } from "@/hooks/use-toast"

interface ClassMeterProps {
  userId: string
  points: number
}

export function ClassMeter({ userId, points }: ClassMeterProps) {
  const [loading, setLoading] = useState(false)
  const { toast } = useToast()

  const handleSimulateAttendance = async () => {
    setLoading(true)
    try {
      // Simulate earning 10 points for a class
      await addPoints(userId, 10, "Class Attendance (Demo)")
      toast({
        title: "Attendance Recorded",
        description: "You earned 10 points!",
      })
    } catch (error) {
      console.error("Error adding points:", error)
      toast({
        title: "Error",
        description: "Failed to record attendance.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
        <CardTitle className="text-sm font-medium">Attendance Points</CardTitle>
        <Zap className="h-4 w-4 text-yellow-500" />
      </CardHeader>
      <CardContent>
        <div className="text-2xl font-bold">{points}</div>
        <p className="text-xs text-muted-foreground">
          Accumulated points from attending classes
        </p>
      </CardContent>
      <CardFooter>
        <Button 
          variant="outline" 
          size="sm" 
          className="w-full" 
          onClick={handleSimulateAttendance}
          disabled={loading}
        >
          {loading ? (
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
          ) : (
            <PlusCircle className="mr-2 h-4 w-4" />
          )}
          Simulate Attendance (+10)
        </Button>
      </CardFooter>
    </Card>
  )
}
