"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { useToast } from "@/hooks/use-toast"
import { spendPoints } from "@/lib/firebase/wallet"
import { Loader2, ShoppingBag } from "lucide-react"

interface PrivilegeStoreProps {
  userId: string
  currentPoints: number
}

interface Perk {
  id: string
  name: string
  description: string
  cost: number
}

const PERKS: Perk[] = [
  {
    id: "lunch-skip",
    name: "Lunch Queue Skip Pass",
    description: "Skip the long line at the cafeteria once.",
    cost: 50,
  },
  {
    id: "jugaad-credits",
    name: "50 Jugaad Bank Credits",
    description: "Get 50 credits to use in Jugaad Bank.",
    cost: 100,
  },
  {
    id: "library-extension",
    name: "Library Book Extension",
    description: "Extend your book loan for an extra week.",
    cost: 30,
  },
]

export function PrivilegeStore({ userId, currentPoints }: PrivilegeStoreProps) {
  const [loading, setLoading] = useState<string | null>(null)
  const { toast } = useToast()

  const handleRedeem = async (perk: Perk) => {
    if (currentPoints < perk.cost) {
      toast({
        title: "Insufficient Points",
        description: `You need ${perk.cost} points to redeem this perk.`,
        variant: "destructive",
      })
      return
    }

    setLoading(perk.id)
    try {
      await spendPoints(userId, perk.cost, `Redeemed: ${perk.name}`)
      toast({
        title: "Redemption Successful",
        description: `You have redeemed ${perk.name}.`,
      })
    } catch (error) {
      console.error("Error redeeming perk:", error)
      toast({
        title: "Redemption Failed",
        description: "Something went wrong. Please try again.",
        variant: "destructive",
      })
    } finally {
      setLoading(null)
    }
  }

  return (
    <Card className="col-span-1 md:col-span-2">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <ShoppingBag className="h-5 w-5" />
          Privilege Store
        </CardTitle>
        <CardDescription>Redeem your attendance points for exclusive perks.</CardDescription>
      </CardHeader>
      <CardContent className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
        {PERKS.map((perk) => (
          <Card key={perk.id} className="flex flex-col justify-between">
            <CardHeader className="pb-2">
              <div className="flex justify-between items-start">
                <CardTitle className="text-base">{perk.name}</CardTitle>
                <Badge variant="secondary" className="ml-2 shrink-0">
                  {perk.cost} pts
                </Badge>
              </div>
              <CardDescription className="text-xs">{perk.description}</CardDescription>
            </CardHeader>
            <CardFooter className="pt-2">
              <Button 
                className="w-full" 
                size="sm" 
                onClick={() => handleRedeem(perk)}
                disabled={currentPoints < perk.cost || loading === perk.id}
              >
                {loading === perk.id ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Redeeming...
                  </>
                ) : (
                  "Redeem"
                )}
              </Button>
            </CardFooter>
          </Card>
        ))}
      </CardContent>
    </Card>
  )
}
